/* App.css */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.main-container {
display: flex;
justify-content: center;
align-items: center; 
}

.chat-container {
  width: 100%;
  max-width: 400px;
  height: 500px;
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  padding: 20px;
}

.chat-box {
  flex: 1;
  overflow-y: scroll;
  margin-bottom: 20px;
  border-bottom: 2px solid #ddd;
  padding-right: 10px;
}

.chat-input {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

input[type='text'] {
  width: 80%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  min-width: 100px;
}

button {
  padding: 10px 15px;
  border: none;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.message {
  margin-bottom: 10px;
}

.message.user {
  text-align: right;
  color: #007bff;
}

.message.bot {
  text-align: left;
  color: #333;
}

/* Mobile responsiveness */
@media (max-width: 600px) {
  .chat-container {
    width: 80%;
    max-width: 100%;
    height: 450px;
    padding: 10px;
  }

  .chat-box {
    padding-right: 5px;
  }

  input[type='text'] {
    width: 70%;
    font-size: 14px;
    padding: 8px;
  }

  button {
    padding: 8px 12px;
    font-size: 14px;
  }
}
